import styles from "./LoginPage.module.css";
import React, {useState} from "react";
import LoginForm from "./LoginForm";
import {RegistrationForm} from "./RegistrationForm";
import {ForgotPasswordForm} from "./ForgotPasswordForm";


export const LoginPage = () => {
    const [registration, setRegistration] = React.useState(false);
    const [resetPassword, setResetPassword] = useState(false);
    return (
        <div className={styles.container}>
                <LoginForm setRegistration={setRegistration} setResetPassword={setResetPassword}/>
                {resetPassword && <ForgotPasswordForm setResetPassword={setResetPassword}/>}
                {registration && <RegistrationForm setRegistration={setRegistration} />}

        </div>
    )
}