import React, {useContext, useEffect, useState} from "react";
import s from './NavBar.module.css'
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import MenuIcon from '@mui/icons-material/Menu';
import {Button, Menu, MenuItem} from "@mui/material";
import {NavLink, useNavigate} from "react-router-dom";
import {Context} from "../../index";
type NavbarPropsType = {
}
export const Navbar =(props: NavbarPropsType)=> {
    const {store}=useContext(Context);
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        store.getStore("Автотовари").then(() => {
            setLoading(false);
        });
    }, []);
    if (loading) return <p>Завантаження...</p>;
    return <div className={s.navbar}>

        <div>
            <Button variant="text" color={'inherit'} onClick={()=>navigate('/AutoShop')}>Головна</Button>
            <Button variant="text" color={'inherit'} onClick={()=>navigate('/products')}>Товари</Button>
        </div>

        <div></div>
        <div></div>
        <div className={s.navDivRight}>
            <Button onClick={()=>navigate('/likes')} color={'inherit'} size={'small'} ><FavoriteBorderIcon/></Button>
            <Button onClick={()=>navigate('/shoppingCart')} color={'inherit'} size={'small'}><ShoppingCartCheckoutIcon/></Button>
            <Button onClick={()=>navigate('/profile')} color={'inherit'} size={'small'}><PersonOutlineIcon/></Button>
        </div>


    </div>
}