import styles from "./LoginPage.module.css";
import {Button, TextField} from "@mui/material";
import React, {useContext, useState} from "react";
import {useTranslation} from "react-i18next";
import {Context} from "../../../index";
import CloseIcon from "@mui/icons-material/Close";

type ForgotPasswordFormProps = {
    setResetPassword: (t:boolean)=>void;
}
export const ForgotPasswordForm = (props: ForgotPasswordFormProps)=>{
    const {store}= useContext(Context);
    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('');
    const [error, setError] = useState('');
    const { t } = useTranslation();
    const getMail = async ()=>{
        try {
            const response = await store.forgotPassword(email)
            if (response?.status === 200) {
                setMessage(t('successSentMail'));
            } else {
                setError(t('errorSentMail'));
            }
        } catch (err) {

        }
    }
    return (<div className={styles.addForm}>
        {message && <div>
            <div style={{color: 'green', fontWeight: "bold", marginTop: '10px'}}>{message}</div>
            <Button onClick={() => props.setResetPassword(false)} color={'error'} variant={'contained'} className={styles.closeButton}><CloseIcon/></Button>
        </div>}
        {error &&<div> <div style={{color: 'red',fontWeight: "bold", marginTop: '10px'}}>{error}</div>
            <Button onClick={() => props.setResetPassword(false)} color={'error'} variant={'contained'} className={styles.closeButton}><CloseIcon/></Button>
        </div>}
        {message===''&& error==='' && <div>
            <h3>{t('headerForgotPass')}</h3>
            <TextField
                label={t('email')}
                onChange={e => setEmail(e.target.value)}
                value={email}
                className={styles.inputField}
            />
            <div className={styles.buttons}>
                <Button
                    variant="contained"
                    color={'success'}
                    onClick={getMail}
                    className={styles.createButton}
                >
                    {t('getMail')}
                </Button>
                <Button
                    variant="contained"
                    color={'error'}
                    onClick={() => props.setResetPassword(false)}
                    className={styles.cancelButton}
                >
                    {t('cancel')}
                </Button>
            </div>

        </div>}

    </div>)
}