import {IUser} from "../models/IUser";
import {makeAutoObservable} from "mobx";
import {IStore} from "../models/IStore";
import StoreService from "../services/StoreService";
import {API_URL} from "../http";
import {AuthResponse} from "../models/response/AuthResponse";
import axios from "axios";
import AuthService from "../services/AuthService";
export default class Store {
    user = {} as IUser;
    store = {} as IStore;
    isAuth = false;
    isLoading = false;


    constructor() {
        makeAutoObservable(this);
        this.loadState()
    }
    setUser(user: IUser) {
        this.user = user;
        this.saveState();
    }
    setLoading(isLoading: boolean) {
        this.isLoading = isLoading;
    }

    setAuth(bool: boolean) {
        this.isAuth = bool;
        this.saveState();
    }
    setStore(store: IStore) {
        this.store=store;
        this.saveState();
    }
    saveState() {
        const state = {
            user: this.user,
            stores: this.store,
        };
        localStorage.setItem("appState", JSON.stringify(state));
    }
    loadState() {
        const savedState = localStorage.getItem("appState");
        if (savedState) {
            const { user, isAuth } = JSON.parse(savedState);
            this.user = user;
            this.isAuth = isAuth;
        }
    }
    async getStore(storeId: any) {
        try {
            const response = await StoreService.getStore(storeId);
            this.setLoading(true)
            this.setStore(response.data);

        } catch (error) {
            console.error('Помилка при отриманні товарів:', error);
        } finally {
            this.setLoading(false)
        }
    }
    async login(email: string, password: string) {
        try {
            const response = await AuthService.login(email, password);
            localStorage.setItem("token", response.data.accessToken);
            this.setAuth(true);
            this.setUser(response.data.user);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }

    async registration(role:string, email: string, password: string, firstName: string, lastName: string, phoneNumber: string) {
        try {
            const response = await AuthService.registration(role, email, password, firstName, lastName, phoneNumber);
            localStorage.setItem("token", response.data.accessToken);
            this.setAuth(true);
            this.setUser(response.data.user);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }

    async logout() {
        try {
           const response = await AuthService.logout();
            localStorage.removeItem("token");
            localStorage.removeItem("appState");
            this.setAuth(false);
            this.setUser({} as IUser);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }

    }

    async checkAuth() {
        try {
            const response = await axios.get<AuthResponse>(`${API_URL}/refresh`, {withCredentials: true});
            localStorage.setItem('token', response.data.accessToken)
            this.setAuth(true);
            this.setUser(response.data.user);
            this.setLoading(true)
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        } finally {
            this.setLoading(false)
        }
    }
    async forgotPassword(email: string) {
        try {
            const response = await AuthService.forgotPassword(email)
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }

    }
    async resetPassword(token: any, newPassword: string) {
        try {
            const response =await AuthService.resetPassword(token, newPassword);
            return response
        } catch (e: any) {
            console.log(e.response?.data?.message);
        }
    }


}