import React from 'react';
import { Select, MenuItem } from '@mui/material';
import { SelectChangeEvent } from '@mui/material/Select';
import { useTranslation } from 'react-i18next';

interface LanguageOption {
    value: string;
    label: string;
    flagCode: string;
}

const languageOptions: LanguageOption[] = [
    { value: 'en', label: 'English', flagCode: 'gb' },
    { value: 'ua', label: 'Українська', flagCode: 'ua' }
];

type LanguageSelectProps = {
    white?: boolean;
};

const LanguageSelect = (props: LanguageSelectProps) => {
    const { i18n } = useTranslation();
    const [selectedLanguage, setSelectedLanguage] = React.useState<string>(i18n.language || 'ua');

    const changeLanguage = (event: SelectChangeEvent) => {
        const language = event.target.value;
        setSelectedLanguage(language);
        i18n.changeLanguage(language);
    };

    return (
        <div>
            <Select
                labelId="language-select-label"
                id="language-select"
                value={selectedLanguage}
                onChange={changeLanguage}
                size="small"
            >
                {languageOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        <img
                            src={`https://flagcdn.com/w40/${option.flagCode}.png`}
                            alt={option.label}
                            style={{ width: 24, height: 16, marginRight: 10, borderRadius: 2 }}
                        />
                        {option.label}
                    </MenuItem>
                ))}
            </Select>
        </div>
    );
};

export default LanguageSelect;
