import {
    Button,
    FormHelperText,
    TextField
} from "@mui/material";
import styles from "./LoginPage.module.css";
import React, {useContext, useState} from "react";
import {Context} from "../../../index";
import {useTranslation} from "react-i18next";
import {PasswordForm} from "../../PasswordForm";

type RegisterProps = {
    setRegistration: any
}
export const RegistrationForm = (props: RegisterProps) => {
    const { t } = useTranslation();
    const [registration, setRegistration] = useState({
        role: "customer",
        firstName: "",
        lastName: "",
        phoneNumber: "+38",
        email: "",
        password1: "",
        password2: "",
    });
    const { store } = useContext(Context);

    const [passwordError, setPasswordError] = useState("");

    const handlePasswordChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setRegistration(prevState => ({ ...prevState, [name]: value }));

        if (name === "password1" || name === "password2") {
            if (value.length < 8) {
                setPasswordError(t('passwordLengthError'));
            } else if (name === "password2" && value !== registration.password1) {
                setPasswordError(t('passwordMismatchError'));
            } else {
                setPasswordError("");
            }
        }
    };

    const registrationButton = () => {
        if (!!passwordError) {
        } else {
            store.registration(
                registration.role,
                registration.email.toLowerCase(),
                registration.password1,
                registration.firstName,
                registration.lastName,
                registration.phoneNumber
            );
        }
    };

    return (
        <div className={styles.addForm}>
            <h3>Уведіть свої дані для реєстрації</h3>
            <TextField
                label={t('firstNameLabel')}
                onChange={e => setRegistration({ ...registration, firstName: e.target.value })}
                value={registration.firstName}
                className={styles.inputField}
            />
            <TextField
                label={t('lastNameLabel')}
                onChange={e => setRegistration({ ...registration, lastName: e.target.value })}
                value={registration.lastName}
                className={styles.inputField}
            />
            <TextField
                label={t('phoneNumberLabel')}
                onChange={e => setRegistration({ ...registration, phoneNumber: e.target.value })}
                value={registration.phoneNumber}
                className={styles.inputField}
            />
            <TextField
                label={t('emailLabel')}
                onChange={e => setRegistration({ ...registration, email: e.target.value })}
                value={registration.email}
                className={styles.inputField}
            />
            <PasswordForm
                passwordError={passwordError}
                label={t('passwordLabel')}
                handlePasswordChange={handlePasswordChange}
                password={registration.password1}
                name={'password1'}
            />
            <PasswordForm
                passwordError={passwordError}
                label={t('repeatPasswordLabel')}
                handlePasswordChange={handlePasswordChange}
                password={registration.password2}
                name={'password2'}
            />
            {passwordError && <FormHelperText error>{passwordError}</FormHelperText>}
            <Button
                variant="contained"
                onClick={registrationButton}
                color={'success'}
            >
                {t('register')}
            </Button>
            <Button
                variant="contained"
                onClick={() => props.setRegistration(false)}
                color={'error'}
            >
                {t('cancel')}
            </Button>
        </div>
    );
};