import { initReactI18next } from "react-i18next";
import i18n from "i18next";
import translationEN from './locales/en.json';
import translationUA from "./locales/ua.json";


const resources = {
    en: {
        translation: translationEN
    },
    ua: {
        translation: translationUA
    }
};
const languageFromBrowser = navigator.language.split('-')[0];
const supportedLanguages = ['en', 'ua'];
const languageToSet = supportedLanguages.includes(languageFromBrowser) ? languageFromBrowser : 'ua';
i18n
    .use(initReactI18next)
    .init({
        fallbackLng: "ua",
        lng: languageToSet,
        debug: true,
        interpolation: {
            escapeValue: false,
        },
        resources,

    });

export default i18n;