import $api from "../http";
import {AuthResponse} from "../models/response/AuthResponse";

export default class AuthService {
    static async login(email: string, password: string ) {
        return $api.post<AuthResponse>('/login', {email, password} )
    }
    static async registration(role:string, email: string, password: string, firstName: string, lastName: string, phoneNumber: string) {
        return $api.post<AuthResponse>('/registration', {role, email, password, firstName, lastName, phoneNumber} )
    }
    static async logout(): Promise<void> {
        return $api.post(`/logout`)
    }
    static async forgotPassword(email: string) {
        return $api.post(`/forgotPassword`, {email})
    }
    static async resetPassword(token: any, newPassword: string){
        return $api.post(`/resetPassword`, {token, newPassword})
    }


}