import React, { useContext, useEffect, useState} from "react";
import {Button, TextField} from "@mui/material";
import {Context} from "../../../index";
import {observer} from "mobx-react-lite";
import {useNavigate} from "react-router-dom";
import styles from './LoginPage.module.css'
import SentimentSatisfiedAltIcon from '@mui/icons-material/SentimentSatisfiedAlt';
import {useTranslation} from "react-i18next";
import i18n from "i18next";
import {PasswordForm} from "../../PasswordForm";
type LoginProps ={
    setRegistration: (bool:boolean)=>void;
    setResetPassword: (bool:boolean)=>void;
}
const LoginForm = (props: LoginProps) => {
    const { t } = useTranslation();
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [error, setError] = useState('');
    const navigate = useNavigate();
    const { store } = useContext(Context);

    useEffect(() => {
        if (localStorage.getItem("token")) {
            store.checkAuth();
        }
    }, []);

    useEffect(() => {
        if (store.isAuth) {
            navigate("/AutoShop");
        }
    }, [store.isAuth, navigate]);

    if (store.isLoading) {
        return <div>{t("loading")}</div>;
    }

    const passwordHandler = (e: React.ChangeEvent<HTMLInputElement>) => {
        setPassword(e.target.value);
    };

    const handleLogin = async () => {
        try {
            const response = await store.login(email, password);
            if (response?.status === 200) {
                navigate("/AutoShop");
            } else {
                setError(t('failedLogin'))
            }

        } catch (err) {
            setError(t('failedLogin'));
        }

    };

    return (
        <div className={styles.content}>
            <h1 className={styles.heading}>Вітаємо в Автомагазині</h1>
            <h3>{t("loginOrRegister")} <SentimentSatisfiedAltIcon/></h3>
            <TextField
                onChange={e => setEmail(e.target.value)}
                value={email}
                label={t("email")}
                className={styles.inputField}
            />
            <PasswordForm
                label={t("password")}
                handlePasswordChange={passwordHandler}
                password={password}
                className={styles.inputField}
            />
            {error !=="" && <div style={{color:"red"}}>{error}</div>}
            <Button variant="contained" onClick={handleLogin}>{t("login")}</Button> <Button onClick={()=>props.setResetPassword(true)} size={'small'} variant={'text'} color={'inherit'}>{t('forgotPassword')}</Button>
            <p>{t('notRegister')} <Button size={'small'} variant={'outlined'} color={'inherit'}
                                          onClick={() => props.setRegistration(true)}>{t('register')}</Button></p>
        </div>
    );
};

export default observer(LoginForm)